<template>
  <div>
    <CRow :gutters="false">
      <CCol md="12">
        <CCard class="mb-1 mr-1 cardstyle">
          <CCardBody>
            <CRow>
              <CCol sm="8" class="mb-0">
                <h4 id="traffic" class="card-title mb-1">目前速度</h4>
              </CCol>
              <CCol sm="4" class="d-none d-md-block text-center">
                
              </CCol>
            </CRow>
            <!-- <MainChart :data="arrSpeed" :labels="arrLabel" :setValue="arrSetSpeed" :time="arrTime" style="height:360px;margin-top:20px;"/> -->
            <LineChart v-if="loaded" ref="lineChart" style="height:400px" :data="chartData"></LineChart>
          </CCardBody>
          <CCardFooter style="background:#DFFEFF">
            <CRow class="text-center">
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>設定速度</h5>
                <h5 style="color:#115756;">{{setSpeed}}</h5>
              </CCol>
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>目前速度</h5>
                <h5 style="color:#115756;">{{currentSpeed}}</h5>
              </CCol>
              <CCol md="4" sm="12" class="mb-sm-2 mb-0">
                <h5>目前米數</h5>
                <h5 style="color:#115756;">{{meters}}</h5>
              </CCol>
            </CRow>
          </CCardFooter>
        </CCard>
      </CCol>
    </CRow>
    <CRow :gutters="false">
      <CCol md="12" sm="12">
        <CCard class="mr-1 mb-1 h-100 cardstyle">
          <CCardBody>
            <MainTable
              :items="arrErrMsg"
              bordered
              small
              caption="點位異常紀錄"
              class="mx-1 my-1 h-100 text-center"
            />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import MainChart from '@/views/charts/MainChart'
import MainTable from '@/views/table/Table.vue'
import LineChart from '@/views/charts/LineChart'
import { getTimeStr } from '@/common/utils.js'


export default {
  name: 'Dashboard',
  props: ['mac'],
  components: {
    MainChart,
    MainTable,
    LineChart
  },
  data() {
    return {
      timer: null,
      setSpeed: '',        // 設定速度
      currentSpeed: '',    // 目前速度
      meters: '',          // 目前米數
      status: 0,           // 0:啟動, 1:停機
      arrLiveErrMsg: [],   // 即時異常訊息陣列
      arrErrMsg: [],       // 今日異常訊息陣列
      arrSpeed: [],        // 目前速度
      arrSetSpeed: [],     // 設定速度
      arrMeters: [],       // 目前米數
      arrTime: [],         // 時間
      arrLabel: [],        // 資料標註
      loaded: false,
    }
  },
  created() {
    this.initGetTodayData()
    // 每秒更新最新的資料
    this.timer = setInterval(() => {
      // 取得設備最新資料
      this.$http.get(`/api/FormingLastData/${this.mac}`)
      .then(res => {
        if(res.data.length>0) {
          this.setSpeed = res.data[0].R120
          this.currentSpeed = res.data[0].R130
          this.meters = res.data[0].R110
          this.status = res.data[0].M666
          let arrErr = res.data[0].arrError

          if(res.data[0].time > Math.max(...this.arrTime)) {
            this.arrSpeed.push(res.data[0].R130)     // 目前速度
            this.arrSetSpeed.push(res.data[0].R120)  // 設定速度
            this.arrMeters.push(res.data[0].R110)    // 目前米數
            this.arrTime.push(res.data[0].time)      // 時間
            this.arrLabel.push(getTimeStr(res.data[0].time))

            this.$refs.lineChart.update()  // update chart

            arrErr.forEach(element => {
              this.arrErrMsg.push(element)
            })
          }
          // check exception
          this.arrLiveErrMsg = []
          for(let i=0; i<arrErr.length; i++) {
            this.arrLiveErrMsg.push(arrErr[i].msg) 
          }

          this.$emit('getStatus', this.status, this.arrLiveErrMsg)
        }
      })
      .catch(err => console.log(err.response.data.message))

    }, 1000)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods: {
    initGetTodayData() {
      // 取得今日當下所有資料
      this.$http.get(`/api/Forming/${this.mac}/${20200427000000}/${20200427235959}`)
      .then(res => {
        this.loaded = true
        this.arrSpeed = []
        this.arrSetSpeed = []
        this.arrMeters = []
        this.arrTime = []
        this.arrLabel = []
        this.arrErrData = []
        this.arrErrMsg = []
        
        for(let i=0; i<res.data.length; i++) {
          this.arrSpeed.push(res.data[i].R130)     // 目前速度
          this.arrSetSpeed.push(res.data[i].R120)  // 設定速度
          this.arrMeters.push(res.data[i].R110)    // 目前米數
          this.arrTime.push(res.data[i].time)      // 時間
          this.arrLabel.push(getTimeStr(res.data[i].time))

          let arrErr = res.data[i].arrError
          arrErr.forEach(element => {
            this.arrErrMsg.push(element)
          })
        }

        this.getChartData()

        // 資料依時間由新到舊進行array reverse
        this.arrErrMsg.reverse()
      })
      .catch(err => console.log(`initGetTodayData(): ${err}`))
    },
    getChartData() {
      this.chartData = Object.assign({})
      this.$set(this.chartData, "labels", this.arrLabel)
      this.$set(this.chartData, "datasets", [])
      this.chartData.datasets.push(
        {
          label: '設定速度',
          data: this.arrSetSpeed,
          backgroundColor: '#f4eeff',
          borderColor: '#5c2a9d',
          borderWidth: 2
        }, 
        {
          label: '目前速度',
          data: this.arrSpeed,
          backgroundColor: '#e4f9ff',    
          borderColor: '#11cbd7',
          borderWidth: 2
        })
    },
  }
}
</script>

<style lang="scss" scoped>
.cardstyle {
  // border: 0px;
  box-shadow: 2px 2px 2px rgba(20%,20%,40%,0.1),4px 4px 6px rgba(20%,20%,40%,0.1),6px 6px 12px rgba(20%,20%,40%,0.2);
}



</style>


