            <h3 v-else class="blink_me"><CBadge color="secondary">無資料</CBadge></h3>
          </CHeaderNavItem>
          <CHeaderNavItem v-for="item in arrErrMsg" :key="item.id" class="d-md-down-none mx-1">
            <h3 class="blink_me"><CBadge color="danger">{{item}}</CBadge></h3>
          </CHeaderNavItem>
        </CHeaderNav>
        <hr class="mt-0 mb-2">

        <DashForming :mac="$route.params.mac" @getStatus="getStatus"></DashForming>
