          <a v-if="role!=='guest'" v-on:click="activetab='3'" v-bind:class="[ activetab === '3' ? 'active' : '' ]">載入未完紀錄</a>
        </div>

        <div class="content">
          <div v-if="activetab ==='1'" class="tabcontent">
