</template>

<script>
import DashForming from '@/views/dashboard/dashForming'

export default {
  components: { DashForming },
  data() {
    return {
      role: '',
      activetab: '1',
      status: 0,
      arrErrMsg: []
    }
  },
  created() {
    this.role = sessionStorage.getItem('ROLE')
  },
  mounted() {
    
  },
  methods: {
    getStatus(status, arrErrMsg) {
      this.status = status
      this.arrErrMsg = arrErrMsg
    },
  }
}
</script>

<style lang="scss" scoped>
/* Style the tabs */
.tabs {
    overflow: hidden;
    margin-bottom: -2px; /* hide bottom border */
    margin-left: 24px;
}

.tabs a{
    float: left;
    cursor: pointer;
    padding: 8px 24px;
    transition: background-color 0.2s;
    border: 1px solid #ccc;
    border-right: none;
    background-color: #f1f1f1;
    border-radius: 8px 8px 0 0;
    font-weight: bold;
}
.tabs a:last-child { 
    border-right: 1px solid #ccc;
}

/* Change background color of tabs on hover */
.tabs a:hover {
    background-color: #aaa;
    color: #fff;
}

/* Styling for active tab */
.tabs a.active {
    background-color: #fff;
    // color: #484848;
    color: #020fc5;
    border-bottom: 2px solid #fff;
    cursor: default;
}

/* Style the tab content */
.tabcontent {
    padding: 6px 6px;
    border: 1px solid #ccc;
    border-radius: 2px;
    box-shadow: 4px 4px 8px #e1e1e1
}

.tabcontent td {
  padding: 0.3em 0.4em;
  color: #484848;
}
.tabcontent td.legend { 
  color: #888; 
  font-weight: bold;
  text-align: right;
}
